ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.bold {
  font-family: 'Roboto-Bold', sans-serif;
}
.extra-bold {
  font-family: 'Roboto-EBold';
}
.italic {
  font-family: 'Roboto-italic', sans-serif;
}

.container {
  padding: 0px 200px;
}

.header {
  background-color: #1f2937;
  padding-top: 20px;
  padding-bottom: 20px;
}
.flex-header nav {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.logo {
  flex: 1;
}
.logo p {
  font-size: 24px;
  color: #f9faf8;
  font-weight: bold;
}
.navbar {
  flex: 1;
}
.navbar ul {
  display: flex;
  flex-direction: row;
  justify-content: end;
}
.item-nav {
  padding: 0 10px;
}
.item-nav a {
  color: #e5e7eb;
  text-decoration: none;
}
.container1 {
  background-color: #1f2937;
  padding-top: 100px;
  padding-bottom: 100px;
}
.flex-banner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.item-banner1 {
  flex-basis: 400px;
}
.item-banner2 {
  flex-basis: 400px;
}

.item-banner2 img {
  object-fit: cover;
  max-width: 100%;
  height: auto;
  border-radius: 10px;
}
.main-button {
  padding: 10px 35px;
  border-radius: 10px;
  color: #f9faf8;
  background-color: #3882f6;
  margin: 5px 0;
  cursor: pointer;
  border: none;
}
.main-text {
  font-size: 48px;
  color: #f9faf8;
}
.second-text {
  font-size: 18px;
  color: #e5e7eb;
}

.container2 {
  padding-top: 50px;
  padding-bottom: 50px;
}
.flex-sale {
  padding-top: 50px;
  padding-bottom: 50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.box {
  flex-basis: 150px;
}
.box-img {
  object-fit: cover;
  max-width: 100%;
  height: auto;
  border-radius: 15px;
  border: 3px solid #3882f6;
  border-radius: 15px;
}
.text {
  text-align: center;
  padding: 5px;
}
.info-text {
  font-size: 36px;
  color: #1f2937;
  text-align: center;
}
.container3 {
  background-color: #e5e7eb;
  padding-top: 100px;
  padding-bottom: 100px;
}
.quote-text {
  font-size: 36px;
  color: #1f2937;
  padding: 0 50px;
}
.name {
  text-align: end;
  padding: 0 50px;
  font-size: 18px;
  color: #1f2937;
}
.container4 {
  padding-top: 100px;
  padding-bottom: 100px;
}
.blue-box {
  background-color: #3882f6;
  display: flex;
  padding: 50px 0;
  justify-content: space-around;
  border-radius: 15px;
}
.box2 {
  padding: 0 50px;
}
.box2 p {
  color: #f9faf8;
}

.box2 p.bold {
  font-size: 18px;
}
.second-button {
  padding: 10px 35px;
  border-radius: 10px;
  color: #f9faf8;
  background-color: #3882f6;
  margin: 5px 0;
  border: 1px solid #f9faf8;
  cursor: pointer;
}
.footer {
  background-color: #1f2937;
  color: #e5e7eb;
  text-align: center;
  padding-top: 20px;
  padding-bottom: 20px;
}
.flex-foot {
  display: flex;
  justify-content: space-between;
}
.foot {
  text-align: left;
  color: #e5e7eb;
}
.foot h2 {
  color: #f9faf8;
  padding: 20px 0;
  font-size: 18px;
}
.foot li {
  padding: 5px 0;
}
.inline {
  display: inline-block;
}
.inline i {
  padding-right: 20px;
  font-size: 36px;
}
.container-img {
  background-size: cover;
  background-repeat: no-repeat;
  padding-top: 100px;
  padding-bottom: 100px;
}
.container-img h2 {
  color: #f9faf8;
}
.ul-about1 {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  padding-bottom: 100px;
}
.flex-about1 {
  flex: 1;
}
.ul-about2 {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}
.flex-about2 {
  flex: 1;
}
.flex-about2 img {
  object-fit: cover;
  width: 100%;
  height: 250px;
}
.flex-about1 h2 {
  font-size: 48px;
}
.flex-about1 p.bold {
  padding-bottom: 25px;
  font-size: 18px;
}
.flex-men {
  display: flex;
  flex-wrap: wrap;
}
.flex-men a {
  text-decoration: none;
  outline: none;
}
.flex-men a:visited {
  color: black;
}
.flex-men1 {
  flex: 1 1 50%;
  background-color: #e5e7eb;
}
.flex-men2 {
  flex: 1 1 33.333%;
  background-color: #e5e7eb;
}
.flex-inside-men {
  display: flex;
  flex-direction: column;
}
.flex-inside-men li {
  flex: 1;
}
.flex-men-img1 {
  height: 900px;
  padding-top: 50px;
  padding-bottom: 50px;
  object-fit: cover;
  width: 100%;
}
.flex-men-img2 {
  height: 352px;
  padding-top: 50px;
  padding-bottom: 50px;
  object-fit: contain;
  width: 100%;
}
.flex-men-img3 {
  object-fit: contain;
  width: 100%;
  height: 400px;
}
.flex-men-img4 {
  width: 100%;
  object-fit: contain;
  height: 400px;
}
.flex-men p {
  text-align: center;
  font-size: 24px;
}
.flex-men1 a,
.flex-men2 a {
  text-align: center;
  display: block;
}
.flex-men1 a:hover,
.flex-men2 a:hover {
  color: #1f2937;
  font-family: 'Roboto-Bold';
}
.men-hover:hover {
  background-color: #f9faf8;
}
.men-hover {
  padding-bottom: 50px;
}
.foot a {
  text-decoration: none;
  outline: none;
}
.foot a:visited {
  color: #f9faf8;
}
.main-button:hover {
  background-color: #3167be;
}
.second-button:hover {
  background-color: #f9faf8;
  color: #3882f6;
}
.flex-men figcaption{
  text-align: center;
}